/* ----------------------------------- COLORS ----------------------------------- */
* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

:root {
  font-size: 15px;
  color: hsla(0deg, 0%, 100%, 0.75);
  font-family: "Inter", sans-serif;
}

body {
  background-color: hsl(233deg, 47%, 7%);
  display: flex;
  justify-content: center;
}

.card {
  background-color: hsl(244deg, 38%, 16%);
  width: calc(100% - 50px);
  min-width: 327px;
  max-width: 600px;
  border-radius: 10px;
  margin: 15vh 0;
  display: flex;
  flex-direction: column;
}
.card .hero {
  width: 100%;
  border-top-left-radius: 10px;
  border-top-right-radius: 10px;
  overflow: hidden;
  position: relative;
}
.card .hero .mobile-img, .card .hero .desktop-img {
  width: 100%;
  filter: contrast(80%);
}
.card .hero .mobile-img {
  display: block;
}
.card .hero .desktop-img {
  display: none;
}
.card .hero .filter {
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  background-color: #af64dd;
  mix-blend-mode: multiply;
}
.card .card-infos {
  padding: 2rem;
  text-align: center;
}
.card .card-infos h1 {
  font-size: 1.8rem;
  font-weight: 700;
  color: hsl(0deg, 0%, 100%);
  margin-top: 0.5rem;
  margin-bottom: 1.3rem;
}
.card .card-infos h1 span {
  color: hsl(277deg, 64%, 61%);
}
.card .card-infos .card-description {
  font-weight: 200;
  line-height: 1.6rem;
  letter-spacing: 0.02rem;
  margin-bottom: 2.7rem;
}
.card .card-infos .stats-wrapper {
  display: flex;
  flex-direction: column;
}
.card .card-infos .stats-wrapper .number {
  font-weight: 700;
  font-size: 1.6rem;
  color: hsl(0deg, 0%, 100%);
  margin-bottom: 0.4rem;
}
.card .card-infos .stats-wrapper .topic {
  font-family: "Lexend Deca", sans-serif;
  font-size: 0.8rem;
  text-transform: uppercase;
  font-weight: 200;
  color: hsla(0deg, 0%, 100%, 0.6);
  letter-spacing: 0.1rem;
}
.card .card-infos .stats-wrapper div:nth-child(1), .card .card-infos .stats-wrapper div:nth-child(2) {
  margin-bottom: 2rem;
}
.card .card-infos .stats-wrapper div:nth-child(3) {
  margin-bottom: 0.4rem;
}

@media screen and (min-width: 950px) {
  body {
    align-items: center;
    height: 100vh;
  }
  .card {
    flex-direction: row-reverse;
    margin: 0;
    max-width: 1110px;
  }
  .card .hero {
    width: 50%;
    border-top-left-radius: 0;
    border-bottom-right-radius: 10px;
  }
  .card .hero .mobile-img {
    display: none;
  }
  .card .hero .desktop-img {
    display: block;
    object-fit: cover;
    height: 100%;
  }
  .card .gap {
    width: 30px;
  }
  .card .card-infos {
    width: 50%;
    padding: 4rem;
    text-align: left;
  }
  .card .card-infos h1 {
    font-size: 2.5rem;
    margin-bottom: 1.7rem;
  }
  .card .card-infos .card-description {
    font-size: 1rem;
    line-height: 1.7rem;
    padding-right: 2rem;
    margin-bottom: 4.8rem;
  }
  .card .card-infos .stats-wrapper {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    margin-right: 4.2rem;
  }
  .card .card-infos .stats-wrapper div {
    margin-bottom: 0px !important;
  }
}